body {
  margin: 0;
  font-family: 'Outfit' 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  color: #fff;
}

h1{
  font-size: 5rem;
}

p{
  font-size: 1rem;
}

.top{
  padding-top: 40px;
}

.app{
  position: relative;
  color: #fff;
  /* background-color: grey; */
  width: 100%;
  height: 100vh;
}

.app:before{
  content: '';
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background: url('./asset/image.jpg') no-repeat center center/cover;
  z-index: -1;
}

.discription{
  display: flex;
  justify-content: right;
 
}

.discription p{
  transform-origin: 0 0;
  transform: rotate(270deg);
}

.bottom{
  background-color: rgba(255, 255, 255, 0.2);
  /* border: 1px solid rgb(129, 118, 118); */
  border-radius: 12px;
  width: 40%;
  margin: 30%;
  padding: 12px;
  font-weight: 100;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 50vh;
  text-align: center;
}

.app .bold{
  font-weight: 700;
}

.input_bar{
  padding: 10px;
  margin: 5px;
  color: #fff;
  font-size: larger;
  border: solid 2px rgba(255, 255, 255, 0.8);
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 5px;
}
